import { ShowDataModal,getBatuColor } from "../../../../actions/datamaster_action";

import {
  React,
  connect,
  Link,
  reduxForm,
  Panel,
  PanelHeader,
  PanelBody,
  ModalGlobal,
  Tabel,
  showModal,
  ToastNotification,
  hideModal,
  deleteData,
  Swal,
  postDataNoTImeOut,
  reset,
  putDataNoTImeOut,
} from "../../../../components/helpers/library";
import HeadBatuShape from "./HeadBatuShape";

const maptostate = (state) => {
  return {
    data: state.datamaster.getBatuColor,
  };
};
class BatuColor extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      isLoading: false,
      columns: [
        {
          dataField: "kode_color",
          text: "Kode Color",
        },
        {
          dataField: "nama_color",
          text: "Nama Color",
        },
        {
          dataField: "action",
          text: "Action",
          csvExport: false,
          headerClasses: "text-center",
          formatter: (rowcontent, row) => {
            let dataEdit = {
              id: row._id,
              kode_color: row.kode_color,
              nama_color: row.nama_color,
            };
            return (
              <div className="row text-center">
                <div className="col-12">
                  <button
                    onClick={() => this.formEdit(dataEdit)}
                    className="btn btn-primary mr-3"
                  >
                    <i className="fa fa-edit"></i>
                  </button>
                  <button
                    onClick={() => this.hapusData(row)}
                    className="btn btn-danger"
                  >
                    <i className="fa fa-trash"></i>
                  </button>
                </div>
              </div>
            );
          },
        },
      ],
    };
    this.handleSubmit = this.handleSubmit.bind(this);
    // this.print = this.print.bind(this);
  }

  handleSubmit(data) {
    // console.log(data);
    this.setState({
      isLoading: true,
    });

    this.state.isEdit
      ? putDataNoTImeOut("batu-color/" + data.id, data)
          .then((res) => {
            this.setState({
              isLoading: false,
            });
            ToastNotification(
              "success",
              res.data.message || "Data Berhasil Disimpan"
            );
            this.props.dispatch(hideModal());
            this.props.dispatch(reset());
            this.props.dispatch(getBatuColor());
          })
          .catch((err) => {
            ToastNotification(
              "info",
              err.response?.data?.message || "Data Gagal Disimpan"
            );
            this.setState({
              isLoading: false,
            });
          })
      : postDataNoTImeOut("batu-color",data)
          .then((res) => {
            this.setState({
              isLoading: false,
            });

            ToastNotification(
              "success",
              res.data.message || "Data Berhasil Disimpan"
            );
            this.props.dispatch(hideModal());
            this.props.dispatch(reset());
            this.props.dispatch(getBatuColor());
          })
          .catch((err) => {
            ToastNotification(
              "info",
              err.response?.data?.message || "Data Gagal Disimpan"
            );
            this.setState({
              isLoading: false,
            });
          });
  }

  componentDidMount() {
    this.props.dispatch(getBatuColor());
  }

  formEdit(data) {
    this.props.dispatch(showModal());
    this.props.dispatch(ShowDataModal(data));
    this.setState({
      isEdit: true,
    });
  }
  showModal() {
    this.props.dispatch(showModal());
    this.props.dispatch(ShowDataModal(true));
    this.setState({
      isEdit: false,
    });
  }

  hapusData(row) {
    Swal.fire({
      // title: "Anda Yakin !!",
      // text: "Ingin Menghapus Data Ini ?",
      html:
        "Apakah Anda Yakin Ingin " +
        "Menghapus " +
        "<h1><b>Kode Color " +
        row.kode_color +
        "</b> ?</h1>",
      icon: "warning",
      position: "top-center",
      cancelButtonText: "Tidak",
      showCancelButton: true,
      confirmButtonText: "OK",
      showConfirmButton: true,
    }).then((result) => {
      if (result.isConfirmed) {
        deleteData("batu-color/" + row._id)
          .then((res) => {
            ToastNotification(
              "success",
              res.data.message || "Data Berhasil Di Hapus"
            );
            this.props.dispatch(getBatuColor());
          })
          .catch((err) => {
            ToastNotification(
              "success",
              err.response?.data?.message || "Data Gagal Di Hapus"
            );
          });
      }
    });
  }
  render() {
    return (
      <div>
        <ol className="breadcrumb float-xl-right">
          <li className="breadcrumb-item">
            <Link to="#">Data Master</Link>
          </li>
          <li className="breadcrumb-item">
            <Link to="#">Data Berlian</Link>
          </li>
          <li className="breadcrumb-item active">Batu Color</li>
        </ol>
        <h1 className="page-header">Batu Color </h1>
        <Panel>
          <PanelHeader>Batu Color</PanelHeader>
          <PanelBody>
            <Tabel
              keyField="kode_color"
              tambahData={true}
              handleClick={() => this.showModal()}
              exportCsv={true}
              data={this.props.data}
              columns={this.state.columns}
              empty={this.props.data}
              textEmpty="Batu Color Kosong"
            />
          </PanelBody>
        </Panel>

        <ModalGlobal
          size="P"
          title={this.state.isEdit ? "Edit Data Color" : "Tambah Data Color"}
          content={
            <HeadBatuShape
              isLoading={this.state.isLoading}
              isEdit={this.state.isEdit}
              onSubmit={(data) => this.handleSubmit(data)}
            />
          }
        />
      </div>
    );
  }
}

BatuColor = reduxForm({
  form: "BatuColor",
  enableReinitialize: true,
})(BatuColor);
export default connect(maptostate)(BatuColor);
